* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.login_section {
  align-items: center;
  display: flex;
  height: 30rem;
  justify-items: center;
  margin: auto;
  padding: 25% 0;
  width: 50rem;
}

.login_wrapper {
  align-items: center;
  border-radius: 30px;
  box-shadow: 20px 15px 60px 8px rgb(102, 102, 102);
  display: flex;
  flex-flow: column nowrap;
  grid-gap: 2rem;
  gap: 2rem;
  justify-items: center;
  padding: 6rem;
}

.login_wrapper .login_logo_wrapper {
  align-items: center;
  display: flex;
  flex-flow: column nowrap;
  justify-items: center;
}

.login_logo_wrapper img {
  width: 60%;
}

.login_form_wrapper {
  align-items: center;
  display: flex;
  flex-flow: column nowrap;
  grid-gap: 0.3em;
  gap: 0.3em;
  justify-items: center;
}

.login_form_wrapper [ type = 'text' ], [ type = 'email' ] {
  border: 0.5px solid rgb(102, 102, 102);
  border-radius: 30px;
  height: 2rem;
  outline: none;
  padding-left: 1rem;
  padding-right: 1rem;
  transition: 0.8s;
  width: 25rem;
}

[ type = 'text' ]:focus, [ type = 'email' ]:focus {
  border-radius: 3px;
  box-shadow: 5px 5px 30px 3px rgba(161, 161, 161, 0.65);
}

.login_btn_wrapper {
  display: flex;
  grid-gap: 3rem;
  gap: 3rem;
  margin: 1rem;
}

.form_btn {
  align-items: center;
  background-color: rgb(65, 34, 94);
  border: none;
  border-radius: 100%;
  color: rgb(252, 252, 252);
  cursor: pointer;
  display: flex;
  font-size: 2rem;
  justify-items: center;
  padding: 0.8rem;
  text-align: center;
  transition: ease-in-out 1s;
}

.form_btn:disabled {
  background-color: rgb(226, 226, 226);
}

.form_btn_text {
  font-size: 1rem;
  opacity: 0;
  position: absolute;
}

.form_btn:hover {
  border-radius: 15px;
  box-shadow: 5px 5px 30px 3px rgba(161, 161, 161, 0.65);
  transition: expansion ease-in-out 1s;
  width: 10rem;
}

.login_btn_wrapper .form_btn:hover .form_btn_text {
  opacity: 1;
  -webkit-transform: translateX(2.5rem);
          transform: translateX(2.5rem);
  transition: ease-in 0.5s;
}

@-webkit-keyframes expansion {

  from {
    border-radius: 100%;
    width: 1rem;
  }

  to {
    border-radius: 15px;
    width: 10rem;
  }

}

@keyframes expansion {

  from {
    border-radius: 100%;
    width: 1rem;
  }

  to {
    border-radius: 15px;
    width: 10rem;
  }

}

