  border-radius: 15px;
  font-size: 1rem;
  padding: 0.3rem 1.5rem;
  text-align: right;
  width: 9rem;
}

* {
  box-sizing: border-box;
  font-family: Montserrat , sans-serif;
  margin: 0;
  padding: 0;
}

.feedback_wrapper {
  align-items: center;
  border-radius: 30px;
  box-shadow: 5px 10px 20px 8px rgb(102, 102, 102);
  display: flex;
  flex-flow: column nowrap;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  justify-content: center;
  margin: 0 auto;
  margin-top: 10rem;
  padding: 3rem;
  width: 50rem;
}

.feedback_wrapper h1 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
}

.feedback_btn {
  align-items: center;
  display: flex;
  grid-gap: 3rem;
  gap: 3rem;
  justify-items: center;
  margin-top: 1rem;
}

.feedback_btn [ type = 'button' ] {
  align-items: center;
  background-color: rgb(65, 34, 94);
  border: none;
  border-radius: 100%;
  color: rgb(252, 252, 252);
  cursor: pointer;
  display: flex;
  font-size: 2rem;
  padding: 0.8rem;
  text-align: center;
  transition: ease-in-out 1s;
}

.feedback_btn_text {
  font-size: 1rem;
  opacity: 0;
  position: absolute;
}

.feedback_config_btn:hover {
  border-radius: 15px;
  box-shadow: 5px 5px 30px 3px rgba(161, 161, 161, 0.65);
  transition: expansion ease-in-out 1s;
  width: 10rem;
}

.feedback_btn .feedback_config_btn:hover .feedback_btn_text {
  opacity: 1;
  -webkit-transform: translateX(2.5rem);
          transform: translateX(2.5rem);
  transition: ease-in 0.5s;
}

@-webkit-keyframes expansion {

  from {
    border-radius: 100%;
    width: 1rem;
  }

  to {
    border-radius: 15px;
    width: 10rem;
  }

}

@keyframes expansion {

  from {
    border-radius: 100%;
    width: 1rem;
  }

  to {
    border-radius: 15px;
    width: 10rem;
  }

}

* {
  box-sizing: border-box;
  font-family: Montserrat , sans-serif;
  margin: 0;
  padding: 0;
}

.header_main_wrapper {
  background-color: black;
  color: aliceblue;
  padding: 1.5rem;
  padding-left: 5rem;
  padding-right: 5rem;
}

.game_wrapper {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 1rem;
}

.game_timer {
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  font-size: 2rem;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center;
  margin: 2rem;
}

.game_timer .timer_icon {
  -webkit-animation: shake infinite 1s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
          animation: shake infinite 1s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  font-size: 5rem;
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.game_box {
  align-items: center;
  border-radius: 30px;
  box-shadow: 5px 10px 20px 8px rgb(102, 102, 102);
  display: flex;
  flex-flow: column nowrap;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center;
  padding: 3rem;
  width: 70rem;

}

.game_answer_btn {
  align-items: center;
  display: flex;
  flex-flow: wrap;
  flex-direction: row;
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
  margin: 30px auto;
  max-width: 780px;
  width: 100%;

}

.game_answer_btn button {
  background: rgb(122, 122, 122);
  border: none;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  font-weight: 700;
  height: 64px;
  max-width: 360px;
  transition: 0.5s;
  width: 100%;
}

.game_answer_btn button:hover {
  box-shadow: 5px 5px 30px 3px rgba(161, 161, 161, 0.65);
}

.next_btn .game_next_btn {
  align-items: center;
  background-color: rgb(65, 34, 94);
