  border-radius: 100%;
  color: white;
  cursor: pointer;
  display: flex;
  font-size: 2rem;
  justify-items: center;
  padding: 1rem;
  text-align: center;
  transition: ease-in-out 1s;
}

.game_answer_btn .correct_click {
  background-color: rgb(36, 90, 50);
}

.game_answer_btn .wrong_click {
  background-color: rgb(92, 35, 35);
}

.game_next_btn_text {
