  transition: expansion ease-in-out 1s;
  width: 10rem;
}

.next_btn .game_next_btn:hover .game_next_btn_text {
  opacity: 1;
  -webkit-transform: translateX(2.5rem);
          transform: translateX(2.5rem);
  transition: ease-in 0.5s;
}

@-webkit-keyframes expansion {

  from {
    border-radius: 100%;
    width: 1rem;
  }

  to {
    border-radius: 15px;
    width: 10rem;
  }

}

@keyframes expansion {

